html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7eaf6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-container {
  margin: 30px auto 0 auto;
  width: 100%;
  text-align: center;
}

.header-title {
  font-size: 40px;
  font-weight: bold;
  color: #113f67;
  box-sizing: border-box;
}

.header-navigation-dropdown {
  margin: 15px;
}

.header-navigation-dropdown span {
  font-size: 18px;
}

.dropdown {
  display: inline-block;
  width: 200px;
  margin: 0 10px;
  z-index: 999;
}


.subheader-container {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px 0;
  width: 100%;
}

.subheader {
  text-align: center;
  color: #113f67;
  border: 3px dashed #38598b;
  font-size: 24px;
  font-weight: 500;
  line-height: 24px;
  border-radius: 5px;
  width: auto;
  padding: 10px 20px;
  margin: 0px 20px;
  z-index: 1;
  max-width: 350px;
}

hr {
  margin-left: 0;
  margin-right: 0;
  width: 30%;
  color: #38598b;
  opacity: 0.7;
}

@media only screen and (max-width: 468px) {
  .subheader {
    font-size: 18px;
  }

  hr {
    display: none;
  }
}

.post-container {
  display: -webkit-flex;
  display: flex;
  width: 700px;
  border-bottom: solid 1px rgba(0,0,0,0.3);
}

.horizontal-image {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.vertical-image {
  height: 600px;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.vertical-image img,
.horizontal-image img {
  padding: 20px;
  border-radius: 30px;
}

.vertical-image img {
  width: 50%;
  box-sizing: border-box;
}
.horizontal-image img {
  height: 70%;
  width: 100%;
  box-sizing: border-box;
}

.description {
  font-size: 14px;
  color: #113f67;
  text-align: justify;
}

.vertical-image .description {
  box-sizing: border-box;
  padding: 20px;
}

.horizontal-image .description {
  box-sizing: border-box;
  padding: 20px;
}

.semester-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  width: 100%;
}

.post-grid {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}











.App {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100%;
  box-sizing: border-box;
  padding: 0 30px;
  margin: 0 auto;
  font-family: "Work Sans", "Roboto", sans-serif;
}

